.hero-img
{
width:100%;
background:rgba(0,0,0,0.5);
height:60vh;
position:relative;

}
.hero-img::before{
    content:"";
    background:url("https://media.istockphoto.com/id/1425828458/photo/young-man-uses-multiple-computers-while-coding-developing-web-site.jpg?b=1&s=170667a&w=0&k=20&c=QSouQfnpzmyfZ4EynL0TyjbHSCbI82Yqaqyc_Wmj3ZM=");
    background-size:cover;
    background-position:center;
    height:100%;
    width:100%;
    position:absolute;
    top:0;
    left:0;
    z-index:-1;
}
.heading
{
    width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}
.hero-img h1{
    font-size:2.4rem;
}
.hero-img p{
    font-size:1.4rem;
    text-align:center;
}
@media screen  and (max-width: 640px){
    .hero-img h1{
        font-size:2rem;
    }  
    
}